.home-container {
    display: flex;
    height: 100vh;
  }
  .left-component {
    flex: 2; 
    background-color: #000000;
    color: white; 
  }
  
  .right-component {
    flex: 15;
    background-image: url('../../public/images/home-img.jpg');
    background-size: cover;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
    overflow-y: hidden;
  }
  .navbar-logo {
    width: 100%; 
  }
  .menu-item {
    padding: 10px 0px 10px 20px;
    font-size: 14px;
    cursor: pointer;
    color: white;
    transition: color 0.3s ease;

  }
  
  .menu-item:hover {
    background-color: #292929;
    border-right: 2px solid #0e8b9f;
  }

  .menu-item.active {
    background-color: #097da048;  /* Highlight color */
    color: white;
    border-right: 2px solid #0e8b9f;
  }

  .nav-icons{
    font-size: 30px;
    margin-right: 10px;
    color: #0e8b9f;
  }
  