.findrecipie-main{
    width: 100%;
    background: linear-gradient(to right, rgba(0, 0, 0, 0.959),rgba(0, 0, 0, 0.842), rgba(0, 0, 0, 0.767));
    height: 100vh;
    overflow-y: scroll;
  }
  .findrecipie-intro-text{
    margin: 10px 30px 0px 30px;
    padding: 20px 20px 0px 20px;
    width: 35%;
    font-size: 50px;
    font-weight: 400;
    color: #e3fbffde;
  }
  .recipies-container{
    margin: 10px 30px 0px 30px;
    padding: 10px 20px 0px 20px;
    color: #dcfaffe7;
  }
  .recipies-container h3{
    font-weight: 400;
    padding-bottom: 10px;
    border-bottom: 1px solid #08a5bd1c;
  }
.findrecipie-search-field{
    background-color: #074e648e;
    border: none;
    height: 40px;
    border-radius: 3px;
    width: 300px;
    margin-left: 5px;
    color: white;
    font-size: 14px;
}
.findrecipie-radio-field{
    margin-right: 50px;
}
.findrecipie-select-field{
    background-color: #074e64a6;
    border: none;
    height: 40px;
    border-radius: 3px;
    color: white;
    width: 10%;
}