.recipe-form {
    color: aliceblue;
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 10px 20px 20px 20px;
    padding: 0px 20px 20px 30px;
  }
  
  .form-sections {
    display: flex;
    gap: 20px;
  }
  
  .form-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  .form-left-preperation {
    flex: 2;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .form-left .add-name{
    height: 35px;
    background-color: #074e64b2;
    border: none;
    color: aliceblue;
    font-size: 15px;
    padding-left: 10px;
    border-radius: 2px;
  }

  .form-left .add-image{
    display: inline-block;
    padding: 10px 20px;
    font-size: 14px;
    color: #fff;
    background-color: #074e64b2;
    border: none;
    border-radius: 2px;
    cursor: pointer;
    text-align: center;
  }

  .form-right {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  .form-right-preperation {
    flex: 5;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  textarea {
    width: 100%;
  }
  
  .form-actions {
    margin-top: 20px;
    text-align: center;
  }
  
  button {
    padding: 8px 16px;
    margin: 0 5px;
    cursor: pointer;
  }
  .custom-upload-container {
    border: 2px dashed #074e64e1;
    border-radius: 5px;
    padding: 10px;
    text-align: center;
    cursor: pointer;
    position: relative;
  }
  
  .custom-upload-container.drag-active {
    border-color: #074e64de;
    background-color: #f1f1f1;
  }
  
  .custom-file-upload {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    font-size: 16px;
    font-weight: bold;
    color: #0b86acf3;
    background-color: transparent;
    cursor: pointer;
    gap: 8px;
  }
  
  .upload-icon {
    font-size: 40px;
  }
  
  input[type="file"] {
    display: none;
  }
  
  .recipe-form p {
    margin-top: 10px;
    font-size: 14px;
    color: #333;
  }

  .recipe-form h3{
    font-weight: 400;
    padding-bottom: 10px;
    border-bottom: 1px solid #08a5bd1c;
  }
  .recipie-description{
    background-color: #074e64b2;
    border: none;
    color: aliceblue;
    font-size: 15px;
    padding: 5px;
    border-radius: 2px;
  }
  .ingredient-list {
    margin-top: 10px;
    padding-left: 20px;
  }
  
  .ingredient-list li {
    font-size: 16px;
    margin-bottom: 5px;
    color: #ffffff;
  }

  .ingredients-select{
    border: none;
    color: aliceblue;
    font-size: 15px;
    padding: 5px;
    border-radius: 2px;
  }  
  .step-input {
    display: flex;
    flex-direction: row;  /* Align items horizontally */
    gap: 10px;  /* Space between textarea and button */
    margin-bottom: 10px;
    align-items: center;  /* Vertically center the items */
  }
  
  .step-input textarea {
    background-color: #074e64b2;
    border: none;
    color: aliceblue;
    font-size: 15px;
    padding: 5px;
    border-radius: 2px;
    width: 100%;  /* Make textarea take available space */
  }
  
  .step-input button {
    padding: 5px 0px;
    cursor: pointer;
    border: none;
    border-radius: 4px;
    background-color: transparent;
    margin-left: auto;  /* Push the button to the right */
  }
  .step-input .icon{
    font-size: 20px;
    color: rgba(255, 0, 0, 0.74);
  }
  .step-input .icon-up{
    font-size: 20px;
    color: rgba(0, 255, 149, 0.74);
  }
  .step-input .icon-down{
    font-size: 20px;
    color: rgba(0, 81, 255, 0.74);
  }
  
  .step-input button:hover {
    color: red;
  }
  
  .step-buttons {
    margin-top: 10px;
  }
  .step-button-add{
    background-color: transparent;
    color: white;
    border: none;
    display: flex;
    justify-content: center;
    gap: 10px;
    align-items: center;
    background-color: #68b6c0d8;
    border-radius: 2px;
  }
 .icon-add{
    font-size: 30px;
    color: rgba(0, 81, 255, 0.74);
  }
  .submit-form{
    background-color: transparent;
    color: white;
    border: none;
    gap: 10px;
    background-color: #2fc08991;
    border-radius: 2px;
    padding: 15px;
    font-size: 16px;
    width: 30%;
  }
  